export const StockMap = observer(() => {
  const { stocks, settings, articles } = useStore();
  const selectedCompany = async (ticker: string) => {
    settings.selectedTicker = ticker;
    const newsData = await articles.getNewsByTicker(ticker);
    newsData.data.data.forEach((el: IArticle) => {
      articles.addArticle(el);
    });
  };
  return (
    <div className=" d-flex flex-wrap" style={{ height: 100 + "%" }}>
      {stocks.dataAr.map((stock, i, ar) => {
        let stockPercent = (stock.value / stocks.dataAr.length) * 100;
        let strPecent = stockPercent.toFixed(2);
        let currentColor = ColorService.getColor(
          stock.value / 10,
          Stocks.red,
          Stocks.green,
        );
        return (
          <CompanyShare
            numberOfElements={stocks.dataAr.length}
            onClickPr={selectedCompany}
            key={stock.ticker}
            data={stock}
          />
        );
      })}
    </div>
  );
});
