import { IColor } from "../interfaces/IColor";

export class ColorService {
  public static getColor(
    percent: number,
    lowPercentColor: IColor,
    highPercentColor: IColor,
  ): IColor {
    const colorDifPercent = this.getRgbParts(lowPercentColor, highPercentColor);
    percent = percent + 1;
    // percent = +percent.toFixed(2);
    //-1 -> 1;0 -> 0.5
    return {
      r: lowPercentColor.r - Math.abs((percent / 2) * colorDifPercent.rDif),
      g: lowPercentColor.g + Math.abs((percent / 2) * colorDifPercent.gDif),
      b: lowPercentColor.b + Math.abs((percent / 2) * colorDifPercent.bDif),
      a: 255,
    };
    if (percent > 0) {
      const newColor: IColor = {
        r:
          highPercentColor.r +
          (1 - percent) * colorDifPercent.rDifPercent * highPercentColor.r,
        g:
          highPercentColor.g -
          (1 - percent) * colorDifPercent.gDifPercent * highPercentColor.g,
        b:
          highPercentColor.b +
          (1 - percent) * colorDifPercent.bDifPercent * highPercentColor.b,
        a: 255,
      };
      return newColor;
    } else {
      const newColor: IColor = {
        r:
          lowPercentColor.r -
          (1 + percent) * colorDifPercent.rDifPercent * lowPercentColor.r,
        g:
          lowPercentColor.g +
          (1 + percent) * colorDifPercent.gDifPercent * lowPercentColor.g,
        b:
          lowPercentColor.b -
          (1 + percent) * colorDifPercent.bDifPercent * lowPercentColor.b,
        a: 255,
      };
      return newColor;
    }
  }

  private static getRgbParts(redColor: IColor, greenColor: IColor) {
    const rDif = Math.abs(redColor.r - greenColor.r);
    const gDif = Math.abs(redColor.g - greenColor.g);
    const bDif = Math.abs(redColor.b - greenColor.b);
    const diffSum = rDif + gDif + bDif;
    console.log("rDif" + +(rDif / diffSum).toFixed(2));
    return {
      rDifPercent: +(rDif / diffSum).toFixed(2),
      rDif,
      gDifPercent: +(gDif / diffSum).toFixed(2),
      gDif,
      bDifPercent: +(bDif / diffSum).toFixed(2),
      bDif,
    };
  }
}
