import { makeAutoObservable } from "mobx";
import { IArticle } from "../interfaces/Article";
import { serverUrl } from "../env";
import { tickersNames } from "../consts/tickers";
import axios from "axios";

export class Articles {
  public data: Article[] = [];
  constructor() {
    makeAutoObservable(this);
  }
  addArticle(a: IArticle) {
    this.data.push(new Article(a));
  }
  getArticlesByTicker(ticker: string) {
    return this.data.filter((article) => {
      return article.ticker === ticker;
    });
  }
  async getNewsByTicker(ticker: string) {
    const resp = await axios.post(serverUrl + "/getNewsByTicker", {
      ticker: ticker,
    });
    return resp;
  }
}
export class Article implements IArticle {
  time: number;
  ticker: string;
  title: string;
  url: string;
  id: number;
  value: number;
  constructor(a: IArticle) {
    makeAutoObservable(this);
    this.title = a.title;
    this.url = a.url;
    this.id = a.id;
    this.ticker = a.ticker;
    this.value = a.value;
    this.time = a.time;
  }
}
export const articlesGl = new Articles();
