import { makeAutoObservable } from "mobx";
import axios from "axios";
import { serverUrl } from "../env";
import { tickersNames } from "../consts/tickers";

export class Settings {
  get selectedTicker(): string {
    return this._selectedTicker;
  }

  set selectedTicker(value: string) {
    this._selectedTicker = value;
  }
  private _selectedTicker: string = "";
  constructor() {
    makeAutoObservable(this);
  }
}
export const settingsGl = new Settings();
