import { makeAutoObservable } from "mobx";
import { IStock } from "../interfaces/Stock";
import { tickers } from "../consts/tickers";
import { randomIntFromInterval } from "../utils/utils";
import { IColor } from "../interfaces/IColor";
import axios from "axios";
import { serverUrl } from "../env";

export class Stocks {
  public data: { [key: string]: Stock } = {};
  public spread: number = 0;
  public static green: IColor = {
    r: 121,
    g: 233,
    b: 70,
    a: 255,
  };
  public static red: IColor = {
    r: 242,
    g: 104,
    b: 70,
    a: 255,
  };
  constructor() {
    makeAutoObservable(this);
    // this.getStocksList(["SBER"]);
  }
  addStock(stock: IStock) {
    this.data[stock.ticker] = new Stock(stock);
  }
  async getStocksList(tickers: string[]) {
    this.data = {};
    const res = await axios.post(serverUrl + "/getStocksList", {
      tickers: tickers,
    });
    res.data.data.forEach((el: IStock) => {
      this.addStock(el);
    });
  }
  get dataAr() {
    return Object.values(this.data);
  }
}
export class Stock implements IStock {
  public height: number = 0;
  public ticker: string = "";
  public value: number = 0;
  public width: number = 0;
  constructor(stockTemp: IStock) {
    makeAutoObservable(this);
    this.height = stockTemp.height;
    this.ticker = stockTemp.ticker;
    this.value = stockTemp.value;
    this.width = stockTemp.width;
  }
}

export const stocksGl = new Stocks();
