import { IStock } from "../../interfaces/Stock";
import { ColorService } from "../../services/ColorService";
import { Stocks } from "../../store/Stocks";

export interface ICompanyShare {
  key?: string;
  data: IStock;
  onClickPr: (ticker: string) => any;
  numberOfElements: number;
}
export const CompanyShare = ({
  data,
  onClickPr,
  numberOfElements,
}: ICompanyShare) => {
  const currentColor = ColorService.getColor(
    data.value / 10,
    Stocks.red,
    Stocks.green,
  );
  const onClick = () => {
    onClickPr(data.ticker);
  };
  return (
    <div
      className={"stock"}
      onClick={onClick}
      style={{
        // minWidth: 60 + "px",
        // minHeight: 60 + "px",
        // maxWidth: 25 + "%",
        width: (100 * Math.sqrt(numberOfElements)) / numberOfElements + "%",
        // height: 100 / numberOfElements + "%",
        // display: "inline-block",
        display: "flex",
        border: 1 + "px solid black",
        background: `rgba(${currentColor.r},${currentColor.g},${currentColor.b},1)`,
        color: "white",
        justifyContent: "center",
        fontWeight: "bold",
        // fontSize: 12 + "px",
      }}
    >
      {data.ticker}
      <br />
      {data.value + "%"}
      {/*<img*/}
      {/*  src={`https://finrange.com/storage/companies/logo/svg/MOEX_${stock.ticker}.svg`}*/}
      {/*  style={{ width: 100 + "%", height: 100 + "%" }}*/}
      {/*/>*/}
    </div>
  );
};
