import { useStore } from "../../store/store";
import { useState } from "react";
import { tickers } from "../../consts/tickers";
import { MultiSelect } from "react-multi-select-component";
import { observer } from "mobx-react-lite";
export const StockMapFilters = observer(() => {
  const { stocks } = useStore();

  const [selectedTickers, setSelectedTickers] = useState([]);
  const onSelectPickerChange = (selected: []) => {
    if (selected.length > 5) {
      alert(
        "   5.      .",
      );
      return;
    }
    setSelectedTickers([...selected]);
  };
  const analyze = (e: any) => {
    e.preventDefault();
    stocks.getStocksList(selectedTickers.map((el: any) => el.value));
  };
  return (
    <form
      className="d-flex"
      onSubmit={(e) => {
        analyze(e);
      }}
    >
      <div className="me-3" style={{ width: 100 + "%" }}>
        <MultiSelect
          options={tickers.map((el) => {
            console.log(el);
            return {
              label: el,
              value: el,
            };
          })}
          value={selectedTickers}
          onChange={onSelectPickerChange}
          labelledBy={"  "}
        />
      </div>
      <div className="d-flex justify-content-end">
        <button type="submit" className="btn btn-primary">
          
        </button>
      </div>
    </form>
  );
});
