import { observer } from "mobx-react-lite";
import { useStore } from "../../store/store";
import { Article } from "./Article";
import { tickersNames } from "../../consts/tickers";

export interface IArticlesList {
  selectedTicker: string;
}
export const ArticlesList = observer(({ selectedTicker }: IArticlesList) => {
  const { articles, settings } = useStore();
  return (
    <div className="card">
      <div className="card-header">
        <div>
          <span className="me-2">:</span>
          <button className="btn btn-outline-secondary me-2"></button>
        </div>
      </div>
      <div style={{ height: 500 + "px", overflowY: "scroll" }}>
        {articles.getArticlesByTicker(selectedTicker).map((article) => {
          return <Article article={article} />;
        })}
      </div>
    </div>
  );
});
