import { createContext, useContext } from "react";
import { Stocks, stocksGl } from "./Stocks";
import { Articles, articlesGl } from "./Articles";
import { Settings, settingsGl } from "./Settings";

const store: {
  stocks: Stocks;
  articles: Articles;
  settings: Settings;
} = {
  stocks: stocksGl,
  articles: articlesGl,
  settings: settingsGl,
};
export const StoreContext = createContext(store);

export const useStore = () => {
  return useContext<typeof store>(StoreContext);
};

export default store;
