import { IArticle } from "../../interfaces/Article";
import { ColorService } from "../../services/ColorService";
import { Stocks } from "../../store/Stocks";

export interface IArticleComp {
  article: IArticle;
}
export const Article = ({ article }: IArticleComp) => {
  const articleRateColor = ColorService.getColor(
    article.value,
    Stocks.red,
    Stocks.green,
  );
  return (
    <div style={{ background: "white" }} className="card mb-2">
      <div className="card-body">
        <a
          style={{ textDecoration: "none", color: "black", fontWeight: "bold" }}
          href={"https://" + article.url}
          target="_blank"
        >
          {article.title}
        </a>
        <div
          className="d-flex justify-content-between"
          style={{ color: "rgb(186,186,186)" }}
        >
          <div>
            <span className="me-2">
              {new Date(article.time * 1000).toLocaleString().slice(0, 10)}
            </span>
            <span className="me-2">{article.url.split("/")[0] + " "}</span>
            <span
              style={{
                color: `rgba(${articleRateColor.r},${articleRateColor.g},${articleRateColor.b})`,
              }}
            >
              {article.value}
            </span>
          </div>
          <div>ee</div>
        </div>
      </div>
    </div>
  );
};
